<template>
  <v-app>
    <v-app-bar color="deep-purple" dense app dark clipped-left flat>
      <v-tooltip bottom>
        <template v-slot:activator="{ on, attrs }">
          <v-app-bar-nav-icon v-on="on" v-bind="attrs" @click="drawer = !drawer" large></v-app-bar-nav-icon>
        </template>
        <span>错误信息栏</span>
      </v-tooltip>

      <v-spacer class="text-right">
        <span class="mr-6 text-h4" style="color:#CFD8DC">忽略：123</span>
        <span class="mr-6 text-h4" style="color:#F4511E">异常：123</span>
        <span class="mr-6 text-h4" style="color:#00B0FF">新增：123</span>
        <span class="mr-6 text-h4" style="color:#FF9100">删除：123</span>
        <span class="mr-4 text-h4" style="color:#1DE9B6">更新：123</span>
      </v-spacer>
      <v-divider inset vertical></v-divider>
      <!-- <template v-slot:extension>
        <span class="mr-6 text-h4" style="color:#CFD8DC">忽略：123</span>
        <span class="mr-6 text-h4" style="color:#F4511E">异常：123</span>
        <span class="mr-6 text-h4" style="color:#00B0FF">新增：123</span>
        <span class="mr-6 text-h4" style="color:#FF9100">删除：123</span>
        <span class="mr-6 text-h4" style="color:#1DE9B6">更新：123</span>
      </template> -->

      <v-tooltip bottom>
        <template v-slot:activator="{ on, attrs }">
          <v-btn icon @click="ratioMinus" v-on="on" v-bind="attrs" large>
            <v-icon>mdi-magnify-minus-outline</v-icon>
          </v-btn>
        </template>
        <span>缩小</span>
      </v-tooltip>

      <v-menu offset-y :close-on-content-click="closeOnContentClick">
        <template v-slot:activator="{ on: menu, attrs }">
          <v-tooltip bottom>
            <template v-slot:activator="{ on: tooltip }">
              <v-btn icon v-bind="attrs" v-on="{ ...tooltip, ...menu }">{{ratio}}</v-btn>
            </template>
            <span>选择倍率</span>
          </v-tooltip>
        </template>
        <div style="background:white;overflow:hidden">
          <v-slider v-model="ratio" vertical dense :min="minRatio" :max="maxRatio">
            <template v-slot:prepend>
              <v-chip class="ma-2" color="#e91e63" style="color:white">{{minRatio}}%</v-chip>
            </template>
            <template v-slot:append>
              <v-chip class="ma-2" color="#e91e63" style="color:white">{{maxRatio}}%</v-chip>
            </template>
          </v-slider>
        </div>
      </v-menu>

      <v-tooltip bottom>
        <template v-slot:activator="{ on, attrs }">
          <v-btn icon @click="ratioAdd" v-on="on" v-bind="attrs" large>
            <v-icon>mdi-magnify-plus-outline</v-icon>
          </v-btn>
        </template>
        <span>放大</span>
      </v-tooltip>

      <v-tooltip bottom>
        <template v-slot:activator="{ on, attrs }">
          <v-btn icon @click="decrement" v-on="on" v-bind="attrs" large>
            <v-icon>mdi-chevron-left</v-icon>
          </v-btn>
        </template>
        <span>上一页</span>
      </v-tooltip>

      <v-menu offset-y :close-on-content-click="closeOnContentClick">
        <template v-slot:activator="{ on: menu, attrs }">
          <v-tooltip bottom>
            <template v-slot:activator="{ on: tooltip }">
              <v-btn
                icon
                v-bind="attrs"
                v-on="{ ...tooltip, ...menu }"
                @click="customerPage = page"
              >{{page}}</v-btn>
            </template>
            <span>选择页码</span>
          </v-tooltip>
        </template>
        <div style="background:white" class="pa-2">
          <v-text-field
            label="页码"
            type="number"
            v-model="customerPage"
            :hint="'最大页数：'+length"
            persistent-hint
            prefix="前往第"
            suffix="页"
            outlined
            dense
            class="mt-2"
            min="1"
            :max="length"
            autofocus
          ></v-text-field>
        </div>
      </v-menu>

      <v-tooltip bottom>
        <template v-slot:activator="{ on, attrs }">
          <v-btn icon @click="increment" v-on="on" v-bind="attrs" large>
            <v-icon>mdi-chevron-right</v-icon>
          </v-btn>
        </template>
        <span>下一页</span>
      </v-tooltip>

      <!-- <v-menu left bottom>
        <template v-slot:activator="{ on: menu, attrs }">
          <v-tooltip bottom>
            <template v-slot:activator="{ on: tooltip }">
              <v-btn icon v-bind="attrs" v-on="{ ...tooltip, ...menu }">
                <v-icon>mdi-tune</v-icon>
              </v-btn>
            </template>
            <span>操作</span>
          </v-tooltip>
        </template>

        <v-list>
          <v-list-item v-for="n in operations" :key="n.id" color="primary" @click="() => {}">
            <v-list-item-title class="font-weight-bold" :class="'active'+n.id">{{ n.title }}</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>-->
      <v-divider inset vertical></v-divider>
      <v-btn color="success" class="mr-2 ml-4" small depressed>通过</v-btn>
      <v-btn color="#ff5722" class="mr-2" small depressed>异常</v-btn>
      <v-btn color="#2196F3" small depressed>发送</v-btn>
    </v-app-bar>
    <v-navigation-drawer v-model="drawer" width="260" app clipped>
      <v-list nav>
        <v-list-item>
          <v-list-item-title class="text-center text-h3">
            <div class="font-weight-medium ">错误信息栏</div>
          </v-list-item-title>
        </v-list-item>
        <v-divider></v-divider>
        <v-list-group v-for="item in 5" :key="item" :value="item == 1 ? true : false">
          <template v-slot:activator>
            <v-list-item-content>
              <v-list-item-title>第{{item}}页</v-list-item-title>
            </v-list-item-content>
          </template>
          <v-list-item
            v-for="subItem in 5"
            :key="subItem + (item * 6)"
            class="ml-4"
            @click="goError"
          >
            <v-list-item-title>{{subItem}}、错误信息</v-list-item-title>
          </v-list-item>
        </v-list-group>
      </v-list>
    </v-navigation-drawer>

    <v-main class="ml-2 mr-2">
      <v-row justify="center">
        <v-col cols="12" sm="12" md="6" class="mt-2 mr-n1">
          <diff-img :imgSrc="diffSrc" :ratio="ratio" :locals.sync="testData" unique="1"></diff-img>
        </v-col>
        <v-divider vertical style="background:#cdcdcd"></v-divider>
        <v-col cols="12" sm="12" md="6" class="mt-2">
          <diff-img :imgSrc="sourceSrc" :ratio="ratio" :locals.sync="testData" unique="2"></diff-img>
        </v-col>
      </v-row>
    </v-main>
  </v-app>
</template>

<script>
export default {
  data: () => ({
    items: [
      {
        key: 1,
        title: "123"
      },
      {
        key: 2,
        title: "123"
      },
      {
        key: 3,
        title: "123"
      },
      {
        key: 4,
        title: "123"
      }
    ],
    page: 1,
    customerPage: 1,
    length: 100,
    nextIcon: "mdi-chevron-right",
    prevIcon: "mdi-chevron-left",
    totalVisible: 7,
    ratio: 100,
    minRatio: 10,
    maxRatio: 150,

    testData: [
      {
        width: 17,
        height: 33,
        top: 199,
        left: 443,
        color: 'orange',
        id: 1
      },{
        width: 20,
        height: 34,
        top: 355,
        left: 822,
        color: 'orange',
        id: 2
      },{
        width: 13,
        height: 39,
        top: 1405,
        left: 701,
        color: 'orange',
        id: 3
      },{
        width: 31,
        height: 51,
        top: 575,
        left: 913,
        color: 'orange',
        id: 4
      }
    ],

    diffData: {
      success: true,
      data: {
        srcUrl:
          "https://yyuan156.oss-cn-beijing.aliyuncs.com/images/2020/07/15/0001/%E5%90%88%E5%90%8C%E6%AD%A3%E6%96%871-0.png",
        destUrl:
          "https://yyuan156.oss-cn-beijing.aliyuncs.com/images/2020/07/15/0001/%E5%90%88%E5%90%8C%E6%AD%A3%E6%96%872-0.png",
      },
      msg: "成功",
      errorCode: "0"
    },
    diffSrc: "",
    sourceSrc: "",
    zoom: 100,
    drawer: true,
    operations: [
      {
        id: 1,
        title: "通过"
      },
      {
        id: 2,
        title: "异常"
      },
      {
        id: 3,
        title: "发送给用户"
      }
    ],
    closeOnContentClick: false
  }),

  created() {
    this.diffSrc = this.diffData.data.destUrl;
    this.sourceSrc = this.diffData.data.srcUrl;
  },

  watch: {
    customerPage: function(val) {
      if (val < 1) {
        this.page = 1;
      } else if (val > this.length) {
        this.page = this.length;
      } else if (val == "-") {
        this.page = 1;
      } else {
        this.page = val;
      }
    }
  },

  mounted() {},

  methods: {
    // goTo() {
    //   if (Number(this.currentPage) > this.length) {
    //     this.page = this.length;
    //   } else if (Number(this.currentPage) < 1) {
    //     this.page = 1;
    //   } else {
    //     this.page = this.currentPage ? Number(this.currentPage) : this.page;
    //   }
    // },
    increment() {
      this.page =
        this.page >= this.length ? this.length : Number(this.page) + 1;
      this.customerPage = this.page;
    },
    decrement() {
      this.page = this.page <= 1 ? 1 : Number(this.page) - 1;
      this.customerPage = this.page;
    },
    ratioMinus() {
      this.ratio =
        this.ratio <= this.minRatio ? this.minRatio : this.ratio - 10;
    },
    ratioAdd() {
      this.ratio =
        this.ratio >= this.maxRatio ? this.maxRatio : this.ratio + 10;
    },
    goError() {
      console.log("error");
    }
  },
  components: {
    DiffImg: () => import("./DiffImg")
  }
};
</script>
<style>
.goToDiv .v-text-field__details {
  display: none;
}
.slider .v-messages {
  display: none;
}
.active1 {
  color: #00df73;
}
.active2 {
  color: #ff5722;
}
.active3 {
  color: #fb8c00;
}
</style>